<template>
  <div id="staff-box">
    <!-- 角色管理 -->
    <van-button
      v-if='applyNum != 0'
      class="staff-apply mt10"
      @click="goApply('/staff-apply','')"
    >
      <span class="applying">您有待审核的角色申请！</span>
      <van-icon
        class="apply-img"
        name="arrow"
      />
    </van-button>
    <van-button
      class="add-staff mt10"
      @click="goNext('/add-staff','')"
    >
      <div style="width: 100%;">
        <van-icon name="plus" />添加角色
      </div>
    </van-button>
    <div
      class="staff-list"
      :style="{height:(applyNum == 0)?('calc(100vh - 110px)'):('calc(100vh - 170px)')}"
    >
      <div
        v-for="(item,i) in currentStaff"
        :key="i"
        class="staff"
        @click="goNext('/staff-info',item)"
      >
        <div
          class="staff-left"
          style="-border:1px solid red;-width:70%;"
        >
          <img
            class="head-img"
            :src="item.sysUser.headUrl"
            alt=""
          >
          <div style="width:100%;">
            <div>{{ item.sysUser.realName || item.sysUser.nickName || '暂无' }} <span style="font-size:14px;color:#989898;">{{ item.sysUser.phone }}</span></div>
            <div class="roleManage">
              <div
                class="f10"
                v-for="(role,i) in item.roleArray"
                :key="i"
              >
                {{ roleObjs[role] }}
              </div>
            </div>
          </div>
        </div>
        <div class="staff-right">
          <span
            v-if="item.roleArray.find(item=>item == 'ROLE_HOTEL_CLEANER')"
            :style="{background:(item.workStatus==0?'#FF505A':'#04BC6B')}"
            class="round"
          ></span>
          <div v-if="item.roleArray.find(item=>item == 'ROLE_HOTEL_CLEANER')">{{ item.workStatus==0?'已下班':'上班中' }}</div>
          <van-icon
            class="right-img"
            name="arrow"
          />
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { GetBusinessHotelOpRelationByhotel } from '@/api/api_staff';
import { GetSystemDictDataByType } from '@/api/api_query';
import { Toast } from 'vant';
export default {
    data() {
        return {
            currentStaff: [],
            hotelRoles: [],
            roleObjs: {},
            applyNum: null
        };
    },
    created() {
        this.$store.commit('setHeader', '员工管理');
        this.getBytype();
        this.getHotelStaffs();
    },
    methods: {
        // 数据查询
        getBytype() {
            GetSystemDictDataByType({ dictType: 'business_hotel_role' }).done((res) => {
                this.roleObjs = {};
                res.data.map((item) => {
                    this.roleObjs[item.dictValue] = item.dictLabel;
                    return this.roleObjs;
                });
            });
        },
        // 获取酒店员工列表
        getHotelStaffs() {
            GetBusinessHotelOpRelationByhotel({ hotelId: this.$route.query.hotelId }).done((res) => {
                res.data.forEach((item) => {
                    item['roleArray'] = item.role.split(',');
                });
                this.currentStaff = res.data;
                this.applyNum = res.order;
            });
        },
        // 员工审核
        goApply(url) {
            this.$router.push({
                path: url,
                query: {
                    hotelId: this.$route.query.hotelId
                }
            });
        },
        // 跳转
        goNext(url, item) {
            let query;
            if (item) {
                query = {
                    hotelId: this.$route.query.hotelId,
                    userId: item.userId,
                    name: item.sysUser.realName || item.sysUser.nickName,
                    phone: item.sysUser.phone,
                    headUrl: item.sysUser.headUrl,
                    roleArray: item.roleArray
                };
                localStorage.setItem('staffInfo', JSON.stringify(query));
            } else {
                query = {
                    hotelId: this.$route.query.hotelId
                };
                localStorage.removeItem('staffInfo');
            }
            this.$router.push({
                path: url,
                query: query
            });
        }
    }
};
</script>
<style scoped>
#staff-box {
    font-size: 0.8rem;
}
.staff-apply {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: #fff;
    width: 100%;
}
.van-button__text {
    display: flex;
    width: 100%;
    justify-content: space-between;
}
.applying {
    color: #048fe5;
}
.apply-img {
    color: #989898;
}
.add-staff {
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #fff;
    color: #000;
    width: 100%;
}
.staff {
    display: flex;
    justify-content: space-between;
    padding: 10px;
    font-size: 0.9rem;
    background: #fff;
    border-bottom: 1px solid #f2f2f2;
}
.staff-list {
    overflow: auto;
    color: #000;
}
.staff-left {
    display: flex;
    text-align: left;
    width: 70%;
}
.head-img {
    width: 40px;
    height: 40px;
    border-radius: 20px;
    margin-right: 10px;
}
.roleManage {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    text-align: center;
}
.staff-right {
    display: flex;
    align-items: center;
}
.right-img {
    margin-left: 10px;
    color: #989898;
}
.round {
    width: 10px;
    height: 10px;
    display: inline-block;
    border-radius: 10px;
    margin-right: 4px;
}
.mt10 {
    margin-top: 10px;
    padding: 10px 20px;
    font-weight: bold;
}
.f10 {
    font-size: 0.6rem;
    color: #5c5c5c;
    border: 1px solid #d2d0d3;
    margin: 1px 2px;
    padding: 2px;
    border-radius: 6px;
    width: 32%;
}
</style>



